        .then((response) => {
          if (response.data == null) {
            console.log("Error");
          }
          return response.data;
        })
        .then((data) => {
          this.moderators = data.moderators;
          this.moderators = this.moderators.sort(function (a, b) {
            if (a.channelName > b.channelName) {
              return 1;
            }
            if (a.channelName < b.channelName) {
              return -1;
            }
            // a must be equal to b
            return 0;
          });
        })
        .catch((e) => {
          console.log(e);
        });
    },
  },
